$color-gray-one: #666666;
$color-gray-two: #b3b3b3;
$color-gray-three: #d8d8d8;
$color-blue-one: #0079ff;

html {
    font-size: 62.5%; // 1rem is 10px
    font-family: 'Lato', sans-serif;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
    font-family: 'montserrat';
}


body {
    overflow-x: hidden;
    font-weight: normal;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    -webkit-tap-highlight-color: transparent;

    font-family: 'Lato', sans-serif;
}


.app-wrapper {
    display: grid;
    justify-content: center;
}




.logo-main, .recent-posts {
    display: grid;
    justify-items: center;
}


.post-topic {
    margin: 0 4px;
}


ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}


.no-content {
    color: $color-gray-one;
    font-size: 14px;
    line-height: 17px;
}



@import './mixins.scss';
@import './app.scss';
@import './search-bar.scss';
@import './recent-posts.scss';
@import './post.scss';
@import './results.scss';